<template>
   <div class="container">


      
      <h1>Test</h1>
<button @click="login">login</button>
      <button @click="test">test</button>
   </div>
   
</template>

<script>
import { REFRESH_TOKEN } from '../store/actions.type';

import JwtService from '@/common/jwt';

import AuthService from '../services/auth';

import UserService from '../services/user';
export default {
   name: 'TestView',
   data () {
      return {
         accessToken:'',
         refreshToken: ''
      }
   },
   beforeMount(){
      let users = UserService.fetch();
      users.then(() => {
            
            
         }).catch(err => console.log(err))
   },
   methods:{
      test(){
         //window.setInterval(this.doRefresh(), 3000);
         // this.$store
         //    .dispatch(REFRESH_TOKEN)
         //    .then(result => {
         //       if(result){
                 
         //       }else{
                 
         //       }        
         //    })
         
         this.doRefresh();
      },
      login(){
         let result = this.doLogin('traders.com.tw@gmail.com','secret');
         result.then(() => {
            
            
         }).catch(err => console.log(err))
      },
      doLogin(email, password){
         return new Promise((resolve) => {
            if (email && password) {
               
               AuthService.login({ email, password })
               .then(model => {

                  JwtService.saveToken(model.accessToken.token,model.refreshToken);
                 
               })
               .catch((err) => {
                  console.log(err);
                  resolve(null);           
               })
            } else {
               resolve(null);
            }
         });
      },
      doRefresh(){
        
         let result = this.refresh(JwtService.getToken(), JwtService.getRefreshToken());
         result.then(result => {
            
            
         }).catch(err => console.log(err))
      },
      refresh(accessToken, refreshToken){
         return new Promise((resolve) => {
            if (accessToken && refreshToken) {
               
               AuthService.refreshToken({ accessToken, refreshToken })
               .then(model => {
                  JwtService.saveToken(model.accessToken.token,model.refreshToken);
                 
                  resolve(true);
               })
               .catch((err) => {
                  resolve(null);           
               })
            } else {
               resolve(null);
            }
         });
      }
   }
}
</script>

